import React,{useState,lazy,Suspense,useEffect} from 'react';
import HeaderComponent from '../header/header';
import Footer from '../footer/footer';
import {BrowserRouter, Routes, Route, HashRouter} from 'react-router-dom'
import { Box, CircularProgress } from "@material-ui/core";
import commonStyles from "../style/commonStyle";
import axios from 'axios';
import '../style/loader.css';
const Dashboard = lazy(()=>import('../dashboard/dashboard'));
const TermsAndConditions = lazy(()=>import('../terms/terms'))

function Mainapp() {
    const classes = commonStyles();
    const [latestRelease, setLatestRelease] = useState(null);
    const [isLoading, setIsLoading] = useState(false);

    const downloadReleaseFile = async () => {
        try {
            if(latestRelease != null){
                let download_link = latestRelease?.assets[0].browser_download_url;
                const link = document.createElement('a');
                link.href = download_link;
                link.click();
            }else{
                getData();
            }
        } catch (error) {
            alert("I am working on it, please try after sometime");
        }
    };

    const getData = async () => {
        setIsLoading(true);
        axios.get('https://api.github.com/repos/Rejjak/billapp/releases/latest').then((res)=>{
            setLatestRelease(res.data);
            setIsLoading(false);
            console.log(res.data)
        }).catch(()=>{
            setIsLoading(false);
        })
    };

    useEffect(()=>{
        getData();
    },[]);

    return (
        <HashRouter>
            {
                isLoading &&
                <div className='loader'/>
            }
            <HeaderComponent latestRelease={latestRelease} onDownload={()=>downloadReleaseFile()}/>
            <Box className={classes.wrapper}>
                <Suspense>
                    <Routes>
                        <Route exact path='/' element={<Dashboard latestRelease={latestRelease} onDownload={()=>downloadReleaseFile()}/>} />
                        <Route path='/terms-conditions' element={<TermsAndConditions/>} />
                    </Routes>
                </Suspense>
            </Box>
            <Footer/>
        </HashRouter>
    );
}

export default Mainapp;